body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin:0  !important;
}

@font-face {
  font-family: 'Minebold';
  src: url('./fonts/Minebold.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

.title{
  font-family: 'Minebold';
  text-align: center;
  color: yellow;
  -webkit-text-stroke: 2px black;
  font-size:90px;
  letter-spacing: 3px;
  padding-top:35vh;
}

.app{
  max-width: 800px;
  margin: auto;
}

.app-containter{
  background-image: url("./images/catbg.avif");
  background-repeat:repeat;
  background-size: 75%;
  height: 100vh;
  width: 100vw;
  top:0;
  position: absolute;
}


.linear-wipe {
  text-align: center;
  background: linear-gradient(to right, #FFF 20%, #FF0 40%, #FF0 60%, #FFF 80%);
  background-size: 200% auto;
  color: #000;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: shine 3s linear infinite;
          animation: shine 3s linear infinite;
}
@-webkit-keyframes shine {
  to {
    background-position: 200% center;
  }
}
@keyframes shine {
  to {
    background-position: 200% center;
  }
}

.meme1,.meme2{
  position: absolute ;
  height:25vh;
  width:25vh;
  background-size:contain ;
}

.meme1{
  background-image:url("./images/banana-cat1.gif");
  top:15vh;
  left:20vw;
}

.meme2{
  background-image:url("./images/banana-cat2.gif");
  top:15vh;
  left:70vw;
}

.token-addy{
  text-align: center;
  font-size:50px;
  padding-top:55px;
  -webkit-text-stroke: 1px black;
  color:white;
  font-weight: bold;
}


.twitter-link a, .twitter-link a:hover{
  color:white;
  text-decoration: none;
  -webkit-text-stroke: 1px black;
}

.twitter-link{
  text-align: center;
  font-size: 80px;
  margin-top:65px;
}

@media all and (max-width: 1400px) {
  .meme1,.meme2{
    height: 95px;
    width: 95px;
  }
}

@media all and (max-width: 750px) {

  .token-addy{
    font-size:25px;
  }

  .meme1,.meme2{
    height: 95px;
    width: 95px;
  }

  .meme1{
    background-image:url("./images/banana-cat1.gif");
    top:15vh;
    left:10vw;
  }
  
  .meme2{
    background-image:url("./images/banana-cat2.gif");
    top:15vh;
    left:60vw;
  }

  .app-containter{
    background-size: 275%;

  }

  .twitter-link{
    font-size:125px;
  }
}